<template>
	<view>
		<u-upload  ref="uUpload" :action="action" :auto-upload="true" name="image"></u-upload>
		<u-button @click="submit">提交</u-button>
		<view v-for="(item,index) in resdata" :key="index">
			<text>{{item.name}}</text>
			<text>{{item.score}}</text>
		</view>
		
	</view>
</template>

<script>
	export default {
		data() {
			return {
				// 非真实地址
				action: 'http://39.103.136.247:5088/api/image',
				resdata: [],
			}
		},
		methods: {
			submit() {
				this.$refs.uUpload.upload();
				// 通过filter，筛选出上传进度为100的文件(因为某些上传失败的文件，进度值不为100，这个是可选的操作)
				setTimeout(()=>{
					this.resdata = this.$refs.uUpload.lists[0].response.result
				},1000)//等待上传
				
				// 如果您不需要进行太多的处理，直接如下即可
				// files = this.$refs.uUpload.lists;
				console.log(this.$refs.uUpload.lists)
			}
		},
	}
</script>
